import {Injectable} from '@angular/core';
import {IconRegistry} from "./icon-database";

@Injectable({
  providedIn: 'root'
})
export class NowboardIconService {
  public registry: { [key: string]: string } = IconRegistry

  constructor() {

  }

  getFromRegistry(key: string) {
    console.log(this.registry[key])
    return this.registry[key];
  }

  addRegistry(key: string, path: string) {
    this.registry[key] = path;
  }
}
