import {Component, Input, OnInit} from '@angular/core';
import {NowboardIconService} from "./nowboard-icon.service";
import {HexToCssConfiguration, hexToCSSFilter} from './hex-to-css-filter';

@Component({
  selector: 'nb-icon',
  template: `
    <span class="nb-icon {{icon}}-x{{size}}" [style]="spanStyleWrapper">
      <img [src]="src" style="{{style}}">
    </span>
  `,
  styles: ['span {display: inline-flex; align-items: center; justify-content: center;}']
})
export class NowboardIconComponent implements OnInit {
  @Input() size: number = 1;
  @Input() color: string = 'white';
  @Input() icon: string = '';
  public style: any = '';
  public spanStyleWrapper: any = {};
  public src: string = '';
  public default_size = 12;

  constructor(public service: NowboardIconService) {
  }

  ngOnInit(): void {
    this.style = [
      `width: ${this.default_size * this.size}px`,
      `height: ${this.default_size * this.size}px`,
      `filter: ${this.filter()}`,
      `transform: scale(${this.size * 0.9})`
    ].join(';')
    this.spanStyleWrapper = {
      width: (this.default_size * this.size) + 'px',
      height: (this.default_size * this.size) + 'px',
    }
    this.src = this.service.getFromRegistry(this.icon);
  }


  private filter() {
    const config: HexToCssConfiguration = {
      acceptanceLossPercentage: 1,
      maxChecks: 10,
    };

    const convert = hexToCSSFilter(this.color, config);
    return convert.filter?.replace(';', '');
  }
}
